    background: #a27ce6;
    border-color: #a27ce6
 }
 
 .theme-purple .chat-widget li.right .chat-info {
    background: #a27ce6;
    color: #fff
 }
 
 .theme-purple .chat-widget li.right .chat-info:before {
    border-left: 10px solid #a27ce6
 }
 
