.fancy-checkbox {
	margin-right: 10px
}

.fancy-checkbox,
.fancy-checkbox label {
	font-weight: normal
}

.fancy-checkbox input[type="checkbox"] {
	display: none
}

.fancy-checkbox input[type="checkbox"]+span {
	display: inline-block;
	vertical-align: middle;
	*vertical-align: auto;
	*zoom: 1;
	*display: inline;
	cursor: pointer;
	position: relative
}

.fancy-checkbox input[type="checkbox"]+span:before {
	display: inline-block;
	vertical-align: middle;
	*vertical-align: auto;
	*zoom: 1;
	*display: inline;
	position: relative;
	bottom: 1px;
	width: 24px;
	height: 24px;
	margin-right: 10px;
	content: "";
	border: 1px solid #ccc;
	border-radius: 3px
}

.fancy-checkbox input[type="checkbox"]:checked+span:before {
	font-family: FontAwesome;
	content: '\f00c';
	font-size: 10px;
	color: #fff;
	text-align: center;
	line-height: 21px
}

.fancy-checkbox.custom-color-green input[type="checkbox"]:checked+span:before {
	color: #22af46;
	background-color: #fff
}

.fancy-checkbox.custom-bgcolor-green input[type="checkbox"]:checked+span:before {
	color: #fff;
	background-color: #22af46;
	border-color: #1b8d38
}

.fancy-radio {
	margin-right: 10px
}

.fancy-radio,
.fancy-radio label {
	font-weight: normal
}

.fancy-radio input[type="radio"] {
	display: none
}



.fancy-radio input[type="radio"]+i {


	display: inline-block;
    vertical-align: middle;
    border-radius: 8px;
    bottom: 1px;
    content: "";
    border: 1px solid #002962;
    width: 18px;
    height: 18px;
    margin-right: 5px;
	position: absolute;
    right: 0;
}
.fancy-radio input[type="radio"]+i:after {
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    display: block;
    position: relative;
    top: 4px;
    left: 4px;
    content: '';
    width: 8px;
    height: 8px;
    border: 1px solid #002962;
}
.fancy-radio input[type="radio"]:checked+i {
	border: 6px solid #002962;

}
.fancy-radio input[type="radio"]:checked+i:after {
	-webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    display: block;
    position: relative;
    top: 3px;
    left: 3px;
    content: '';
    width: 2px;
    height: 2px;
	border: 1px solid #fff;
    background-color: #ffffff;
}
label.fancy-radio,  input[type="radio"]+i {
    width: 100%;
    display: inline-block;
    position: relative;
	cursor: pointer;
}


.fancy-radio.custom-color-green input[type="radio"]:checked+span i:after {
	background-color: #53D76A
}

.fancy-radio.custom-bgcolor-green input[type="radio"]:checked+span i {
	background-color: #53D76A
}

.fancy-radio.custom-bgcolor-green input[type="radio"]:checked+span i:after {
	background-color: #fff
}

