	font-size: 13px;
	cursor: pointer
}

.event-control:hover {
	background-color: #fff
}

.event-control a {
	color: #444;
	cursor: pointer;
	opacity: .5
}

.event-control a:hover {
	opacity: 1
}

.event-control[class*='bg-']:not(.bg-default):not(.bg-white) a {
	color: #444 !important;
	opacity: 1
}

.event-control[class*='bg-']:not(.bg-default):not(.bg-white) a:hover {
	color: #fff !important
}

.fc-overlay {
	display: none;
	position: absolute;
	min-width: 260px;
	padding: 15px 0;
	top: auto;
	left: 50%;
	margin-left: -150px;
	z-index: 1000;
	color: #444 !important
}

.fc-overlay.left {
	left: 100%;
	top: -15px;
	padding: 0 10px;
	margin: 0
}

.fc-overlay.right {
	left: auto;
	right: 100%;
	top: -15px;
	padding: 0 10px
}

.fc-overlay .panel {
	padding: 10px
}

